@import '../pages/Selection/parts/CurrenciesSelectionConfirmButton/button';

body {
    overflow-y: scroll;
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell',
        'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

#root {
    min-height: 100vh;
    display: flex;
    align-items: center;
    flex-direction: column;
    @media (max-width: 640px) {
        background: url('../graphics/background_640.jpg') no-repeat center center fixed;
        background-size: cover;
    }
    @media (min-width: 641px) and (max-width: 1280px) {
        background: url('../graphics/background_1280.jpg') no-repeat center center fixed;
        background-size: cover;
    }

    @media (min-width: 1281px) {
        background: url('../graphics/background_1920.jpg') no-repeat center center fixed;
        background-size: cover;
    }

    background-size: cover;
    background-repeat: no-repeat;
    color: white;
    & * {
        box-sizing: border-box;
    }
}

@keyframes fade_in {
    0% {
        opacity: 0;
    }
    70% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

.selector-item__coin {
    color: #1a86db;
    width: 300px;
    margin-bottom: 8px;
    background-color: white;
    border-radius: 2px;
    border: 2px solid #1a86db;
    display: flex;
    align-items: center;
    justify-content: space-around;
    padding: 7px 2px;
}

.selector-item {
    width: 300px;
}

.virtualized {
    background-color: #2c303a;
    animation: fade_in 1s ease-in-out forwards;
    box-shadow: inset 0 0 5px #1e1f26, 0 20px 25px -5px rgba(0, 0, 0, 0.1),
        0 10px 10px -5px rgba(0, 0, 0, 0.04);
    color: white;
    padding: 20px;
    font-size: 1rem;
    border: 2px solid #1e1f26;
    border-radius: 2px;
    margin: 2vh 0;
    cursor: pointer;
    &:hover {
        border: 2px solid #ee760a;
    }

    & .Select-placeholder {
        color: white;
        margin-bottom: 15px;
        font-family: 'Lato', sans-serif;
    }

    & input {
        width: 100% !important;
        margin: 0 auto;
        display: block;
        border: 1px solid white;
        border-radius: 1px;
        background: transparent;
        color: white;
        font-size: 1rem;
        padding: 0 2px;
        box-shadow: inset 0px 0px 2px 1px rgba(255, 255, 255, 0.54);
    }
    & .Select-menu-outer span:hover {
        background-color: white;
    }
}
.DataContainer {
    ///aktualny
    margin: 0 auto;
    height: 110px;
    width: 100%;
    max-width: 1200px;
    position: relative;
    padding-top: 15px;
    padding-bottom: 15px;
    margin-top: 5px;
    margin-bottom: 5px;
    background-color: transparent;
    background: mintcream;
    border-top: 2px solid #4b7de6;
    border-right: 2px solid #4b7de6;
    border-bottom: 2px solid #4b7de6;
    color: #726b68;
    box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
    display: flex;
    justify-content: space-between;
    border-radius: 0.25rem;
    align-items: center;
}
.Select-multi-value-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.DataContainer::before {
    min-width: 110px;
    content: 'ALTCOINS';

    @media (max-width: 1000px) {
        min-width: 246px;
    }
    height: 20px;
    background-color: #2692c3;
    background-color: #4b7de6;
    border-radius: 0.25rem 0.25rem 0 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: absolute;
    left: -45px;

    @media (max-width: 1000px) {
        left: -112.5px;
    }

    color: white;
    font-size: 10px;
    transform: rotate(270deg);
}

@media (max-width: 1000px) {
    .DataContainer {
        border: none;
        flex-direction: column;
        height: unset;
        border-radius: 0 0 0.25rem 0.25rem;
    }
    .DataContainer::before {
        width: inherit;
        left: 0;
        top: -20px;
        transform: none;
    }
}

/**
 * Tooltip
 */

/* Add this attribute to the element that needs a tooltip */
[data-tooltip] {
    position: relative;
    z-index: 2;
    cursor: pointer;
}

/* Hide the tooltip content by default */
[data-tooltip]:before,
[data-tooltip]:after {
    visibility: hidden;
    opacity: 0;
    pointer-events: none;
}

/* Position tooltip above the element */
[data-tooltip]:before {
    position: absolute;
    bottom: 100%;
    left: 50%;
    margin-bottom: 5px;
    margin-left: -80px;
    padding: 7px;
    min-width: 160px;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    border-radius: 3px;
    background-color: #000;
    background-color: hsla(0, 0%, 20%, 0.9);
    color: #fff;
    content: attr(data-tooltip);
    text-align: center;
    font-size: 14px;

    @media screen and (max-width: 760px) {
        font-size: 12px;
    }

    @media screen and (max-width: 550px) {
        font-size: 10px;
    }

    line-height: 1.2;
}

[data-tooltip]:after {
    position: absolute;
    bottom: 150%;
    left: 50%;
    margin-left: -5px;
    width: 0;
    border-top: 5px solid #000;
    border-top: 5px solid hsla(0, 0%, 20%, 0.9);
    border-right: 5px solid transparent;
    border-left: 5px solid transparent;
    content: ' ';
    font-size: 0;
    line-height: 0;
}

/* Show tooltip content on hover */
[data-tooltip]:hover:before,
[data-tooltip]:hover:after {
    visibility: visible;
    animation: tooltip 3s forwards;
}

@keyframes tooltip {
    0% {
        opacity: 1;
    }
    50% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
}

.clock {
    position: fixed;
}

.logo {
    margin-right: 3px;
}
